<template>
  <el-container v-loading="loading"
  	 element-loading-text="加载中"
    element-loading-spinner="el-icon-loading"
    element-loading-background="rgba(0, 0, 0, 0.8)">
	  <el-header class="headFixed">
	  	<div class="logo">
	  		<p>个人展示</p>
	  	</div>
	  	<el-menu default-active="profile" class="navBar" mode="horizontal" @select="handleSelect">
			  <el-menu-item index="profile">profile</el-menu-item>
			  <el-menu-item index="picture">picture</el-menu-item>
			  <el-menu-item index="project">project</el-menu-item>
			  <el-menu-item index="remarks">remarks</el-menu-item>
			</el-menu>
	  </el-header>
	  <el-main>
	  	<section class="box" id="profile">
  			<div class="profile-img">
  				<img src="static/images/head.jpg" />
  			</div>
  			<div class="profile-content">
  				<p>每逢新旧年交替时候，我就被笼罩在一股茫然的思绪里，逃也逃不脱，避也避不开，唯有让心事在静静流逝的时光里苍茫成河。
是从什么时候开始的呢，我又变得如此感时伤怀？我很羡慕那些在年终盘点总结的人们，过去的一年于他们来说，是大刀阔斧的一年，是硕果累累的一年。再不济的话，也是豪情万丈的一年。而在我那逝去的时光里，平静如水，不起一丝波澜，一年又一年。我没有盘点，因为我没有波澜壮阔；我不会去总结，因为我的总结从来就没有逼近我的心灵。我知道这于我来说，显得多么无厘头。</p>
  			</div>
	  	</section>
	  	<section class="bg bg-s"></section>
	  	<section class="box" id="picture">
	  		<el-tabs tab-position="left" style="height: 487px;">
			    <el-tab-pane label="pc">
			    	<el-row :gutter="20">
						  <el-col :span="4" v-for="(item,index) in picList1" :key="index" >
						  	<el-card>
						  		<div class="box-card">
							  		<img :src="item.imgSrc" class="image" @click="previewImg(picList1,index)">
	    							<div>
	        						<span>{{item.imgDesc}}</span>
	        					</div>
        					</div>
								</el-card>
					  	</el-col>
					  </el-row>
					  <el-row :gutter="20">
						  <el-col :span="4" v-for="(item,index) in picList2" :key="index" >
						  	<el-card>
						  		<div class="box-card">
							  		<img :src="item.imgSrc" class="image" @click="previewImg(picList2,index)">
	    							<div>
	        						<span>{{item.imgDesc}}</span>
	        					</div>
        					</div>
								</el-card>
					  	</el-col>
					  </el-row>
			    </el-tab-pane>
			    <el-tab-pane label="wap">
			    	<el-row :gutter="20">
						  <el-col :span="4" v-for="(item,index) in picList3" :key="index" style="margin-bottom: 10px;">
						  	<el-card>
						  		<div class="box-card">
							  		<img :src="item.imgSrc" class="image" @click="previewImg(picList3,index)">
	    							<div>
	        						<span>{{item.imgDesc}}</span>
	        					</div>
        					</div>
								</el-card>
					  	</el-col>
					  </el-row>
			    </el-tab-pane>
			    <el-tab-pane label="app">app端</el-tab-pane>
			  </el-tabs>
	  	</section>
	  	<section class="bg bg-b"></section>
	  	<section class="box" id="project">
	  		<el-carousel :interval="4000" type="card" height="320px">
			    <el-carousel-item v-for="(image,index) in projectImg" :key="index">
  					<img :src="image.imgSrc"  @click="openIframe(image.imgTitle,image.imgUrl)" />
			    </el-carousel-item>
			  </el-carousel>
	  	</section>
	  	<section class="box" id="remarks">
	  		<p>我喜欢这样安静地坐下来，想一些似有若无的心事。尽量不去想那些飞逝的时光，尽量不去想那些形色匆忙的脚步，似乎是在害怕，在这高速运转的时空里，自己有一天会化作一堆虚无。还记得那一部《岁月神偷》的电影吗？真是要感谢来自香港的导演罗启锐，影片给我们芸芸众生留下太多的唏嘘。在如水一般逝去的时光里，皱纹爬上你的眼角，白发蔓延我的双鬓。岁月才是最好的神偷，偷走了我的青春和你的美好。
				就任这时光慢慢流淌吧，对于大多数人来说，我们无非就是浩瀚时空的一些微尘。</p>
	  	</section>
	  </el-main>
		<back-to-top transitionName="fade" :customStyle="myBackToTopStyle" :visibilityHeight="100" :backPosition="0"></back-to-top>
	  <el-footer>
	  	<p>本页面由 vue + vue-router + element-ui + sass 构成</p>
	  </el-footer>
	  <el-dialog :title="iframeTitle" :visible.sync="dialogVisible">
  		<iframe :src="iframeUrl"></iframe>
		  <div slot="footer" class="dialog-footer">
		  	<span>访问地址：</span>
		  	<a :href="projectUrl">{{projectUrl}}</a>
		  </div>
		</el-dialog>
		<!--预览图片弹窗-->
		<transition name="fade">
			<div class="previewBox" v-show="previewVisible" >
				<input id="escClose" @keyup.esc="closePreview"style="left: -5000%;top: -5000%;"/>
				<div class="previewImg-mask" @click="closePreview()"></div>
				<div class="previewImg-container">
					<div class="preview-close" @click="closePreview()"></div>
					<div class="arrow">
				    <span class="prev" @click="prevImg">&lt;</span>
				    <span class="next" @click="nextImg">&gt;</span>
			    </div>
					<div class="preview-img">
						<img :src="previewSrc" />
					</div>
					<div class="preview-text">
						<span>{{previewText}}</span>
					</div>
				</div>
			</div>
		</transition>
	</el-container>
</template>

<script>
import BackToTop from '@/components/BackToTop'
export default {
  name: 'home',
	components: { BackToTop },
  data () {
    return {
    	loading: false,
    	scrollPosition: 0,
    	myBackToTopStyle: {
    		zIndex: '1',
        right: '30px',
        bottom: '30px',
        width: '40px',
        height: '40px',
        'border-radius': '4px',
        'line-height': '45px', // 请保持与高度一致以垂直居中 Please keep consistent with height to center vertically
        background: '#e7eaf1'// 按钮的背景颜色 The background color of the button
	    },
	    iframeUrl: '',
	    projectUrl: '',
	    iframeTitle: 'title',
	    picList1:[{
	    	imgSrc: 'static/images/bg1.jpg',
	    	imgDesc: 'rem1'
	    },{
	    	imgSrc: 'static/images/bg2.jpg',
	    	imgDesc: 'rem2'
	    },{
	    	imgSrc: 'static/images/index_bg1.png',
	    	imgDesc: 'rem3'
	    },{
	    	imgSrc: 'static/images/head.jpg',
	    	imgDesc: 'rem4'
	    },{
	    	imgSrc: 'static/images/pic.jpg',
	    	imgDesc: 'rem5'
	    },{
	    	imgSrc: 'static/images/index_bg.png',
	    	imgDesc: 'rem6'
	    }],
	    picList2:[{
	    	imgSrc: 'static/images/pic.jpg',
	    	imgDesc: '详细信息1'
	    },{
	    	imgSrc: 'static/images/bg1.jpg',
	    	imgDesc: '详细信息2'
	    },{
	    	imgSrc: 'static/images/index_bg.png',
	    	imgDesc: '详细信息3'
	    },{
	    	imgSrc: 'static/images/head.jpg',
	    	imgDesc: '详细信息4'
	    },{
	    	imgSrc: 'static/images/bg2.jpg',
	    	imgDesc: '详细信息5'
	    },{
	    	imgSrc: 'static/images/index_bg1.png',
	    	imgDesc: '详细信息6'
	    }],
	    picList3:[{
	    	imgSrc: 'static/images/bg1.jpg',
	    	imgDesc: 'rem1'
	    },{
	    	imgSrc: 'static/images/bg2.jpg',
	    	imgDesc: 'rem2'
	    },{
	    	imgSrc: 'static/images/index_bg1.png',
	    	imgDesc: 'rem3'
	    },{
	    	imgSrc: 'static/images/head.jpg',
	    	imgDesc: 'rem4'
	    },{
	    	imgSrc: 'static/images/pic.jpg',
	    	imgDesc: 'rem5'
	    },{
	    	imgSrc: 'static/images/index_bg.png',
	    	imgDesc: 'rem6'
	    },{
	    	imgSrc: 'static/images/bg1.jpg',
	    	imgDesc: 'rem1'
	    },{
	    	imgSrc: 'static/images/bg2.jpg',
	    	imgDesc: 'rem2'
	    },{
	    	imgSrc: 'static/images/index_bg1.png',
	    	imgDesc: 'rem3'
	    },{
	    	imgSrc: 'static/images/head.jpg',
	    	imgDesc: 'rem4'
	    },{
	    	imgSrc: 'static/images/pic.jpg',
	    	imgDesc: 'rem5'
	    },{
	    	imgSrc: 'static/images/index_bg.png',
	    	imgDesc: 'rem6'
	    }],
	    projectImg: [{
	    	imgTitle: 'layui后台操作系统',
	    	imgSrc: 'static/images/layui-system.png',
	    	imgUrl: 'static/project/layuiSystem/goods.html',
	    },{
	    	imgTitle: 'vue练手企业站',
	    	imgSrc: 'static/images/daiyun.png',
	    	imgUrl: 'static/project/daiyun/index.html',
	    },{
	    	imgTitle: 'vue+mint-ui练手移动端',
	    	imgSrc: 'static/images/mint-ui.png',
	    	imgUrl: 'static/project/mint-ui-wap/index.html',
	    },{
	    	imgSrc: 'static/images/pic.jpg',
	    	imgUrl: '',
	    }],
	    dialogVisible: false,
	    previewVisible: false,
	    previewSrc: "",
	    previewText: "",
	    previewList: {
	    	list:[],
	    	mask: 0,
	    },
	    timer: null
    }
  },
  created: function(){
  	this.loading = true;
  },
  methods: {
	  handleSelect(key, keyPath) {
	  	if(document.getElementById(key)){
	  		this.scrollPosition = document.getElementById(key).offsetTop-60;
      	window.scrollTo(0, this.scrollPosition)
	  	}
	  },
	  openIframe(imgTitle,imgUrl) {
	  	if(imgTitle){
	  		this.iframeTitle = imgTitle;
	  	}
	  	if(imgUrl){
	    	this.dialogVisible = true;
	  		this.iframeUrl = imgUrl;
	  		this.projectUrl = "http://" + location.host + "/" + imgUrl;
	  	}else{
	  		this.$message.error('暂无项目链接！');
	  	}
	  },
	  previewImg(pList,pIndex){
	  	this.previewVisible = true;
	  	this.previewList.list = pList;
	  	this.previewList.mask = pIndex;
	  	document.body.style.overflow = "hidden";
	  	document.body.style.paddingRight = "17px";
	  	this.changeImg(pIndex);
  		this.timer = setInterval(function(){
  			document.getElementById("escClose").focus();
  		},300)
	  },
	  closePreview(){
	  	this.previewVisible = false;
	  	document.body.style.overflow = "";
	  	document.body.style.paddingRight = "";
	  	clearInterval(this.timer);
	  },
	  prevImg(){
	  	this.previewList.mask -= 1;
	  	if(this.previewList.mask < 0){
	  		this.previewList.mask = this.previewList.list.length -1;
	  	}
	  	this.changeImg(this.previewList.mask);
	  },
	  nextImg(){
	  	this.previewList.mask += 1;
	  	if(this.previewList.mask >= this.previewList.list.length){
	  		this.previewList.mask = 0;
	  	}
	  	this.changeImg(this.previewList.mask);
	  },
	  changeImg(index){
	  	this.previewSrc = this.previewList.list[index].imgSrc;
	  	this.previewText = this.previewList.list[index].imgDesc;
	  }
	},
	mounted: function(){
		var _this = this;
		_this.loading = false;
	}
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
	.el-header {
		&.headFixed {
			padding-top: 61px;
			.navBar{
				position: fixed;
				top: 0;
				left: 240px;
				right: 0;
				z-index: 11;
			}
			.logo {
				position: fixed;
				z-index: 11;
				top: 0;
				left: 0;
				width: 240px;
			}
		}
		.el-menu--horizontal {
			&.navBar {
				text-align: right;
				.el-menu-item {
					float: none;
					display: inline-block;
				}
			}
		}
		.logo {
			background-color: #fff;
			border-bottom: 1px solid #e6e6e6;
			p {
				font-size: 24px;
				color: #333;
				margin: 0;
				font-weight: bold;
				line-height: 60px;
				text-align: center;
			}
		}
	}
	.el-main {
		padding: 0;
		.bg {
			&.bg-s {
				background: url(../../assets/images/bg1.jpg) no-repeat center center;
				background-size: 100%;
				height: 100px;
			}
			&.bg-b {
				background: url(../../assets/images/bg2.jpg) no-repeat fixed center center;
				height: 300px;
			}
		}
		.box {
			padding: 30px 20px;
			border-bottom: 1px solid #eee;
			&#profile {
				.profile-img {
					width: 100px;
					height: 100px;
					border-radius: 50%;
					overflow: hidden;
					margin: 0 auto;
					border: 1px solid #eee;
					img {
						width: 100%;
						height: 100%;
					}
				}
				.profile-content {
					font-size: 16px;
					color: #333;
					p {
						text-align: center;
						line-height: 30px;
						padding: 0 40px;
					}
				}
			}
			&#picture {
				.el-tab-pane {
					padding: 10px;
					.el-row {
				    margin-bottom: 10px;
				    &:last-child {
							margin-bottom: 0;
						}
				    .box-card {
				    	overflow: hidden;
			    		.image {
				    		height: 178px;
		    		    vertical-align: middle;
				    		text-align: center;
			    			cursor: pointer;
				    	}
				    	div {
				    		overflow: hidden;
				    		white-space: nowrap;
				    		text-overflow: ellipsis;
				    		padding-top: 10px;
				    		span {
				    			font-size: 14px;
				    			color: #666;
				    		}
				    	}
				    }
				  }
				}
			}
			&#project {
				/*background: url(../../assets/images/bg2.jpg) no-repeat fixed;*/
				.el-carousel__item {
					display: flex;
					align-items: center;
    			justify-content: center;
    			background-color: rgba(255,255,255,.8);
			  }
			}
			&#remarks {
				p {
					margin: 0;
					text-align: center;
					line-height: 30px;
					padding: 0 40px;
				}
			}
		}
	}
	.el-footer {
		padding: 10px 20px;
		height: auto !important;
		p {
			text-align: center;
			margin: 0;
			font-size: 14px;
			color: #ccc;
		}
	}
	.el-dialog {
		iframe {
			width: 100%;
			height: 100%;
			border: none;
		}
		.dialog-footer {
			a {
				color: #666;
				&:hover {
					color: dodgerblue;
				}
			}
		}
	}
	.previewBox {
		position: fixed;
    z-index: 2000;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
		.previewImg-mask {
	    position: absolute;
	    z-index: 1;
	    background-color: rgba(0,0,0,.5);
	    top: 0;
	    right: 0;
	    bottom: 0;
	    left: 0;
	    -webkit-transition: opacity .3s;
	    transition: opacity .3s;
	  }
	  .previewImg-container {
	  	.preview-close {
  			position: absolute;
  			top: 0;
  			right: 0;
  			width: 45px;
  			height: 45px;
  			cursor: pointer;
  			z-index: 3;
				background: url(../../assets/images/close.png) no-repeat center center;
				background-size: 100% 100%;
	  	}
			.preview-img {
				display: flex;
	    	text-align: center;
				align-items: center;
	    	justify-content: center;
				height: 80vh;
				margin: 5vh 0;
				img {
					position: relative;
	  			z-index: 2;
					max-width: 100%;
				  max-height: 100%;
				}
			}
			.preview-text {
				display: flex;
	    	text-align: center;
	    	justify-content: center;
				height: 8vh;
				padding: 0 10px;
				margin-bottom: 2vh;
				span {
					position: relative;
	  			z-index: 2;
					font-size: 20px;
					margin: 0;
					color: #fff;
				}
			}
			.arrow {
				span {
					position: absolute;
	  			z-index: 3;
					width: 50px;
					height: 50px;
					line-height: 50px;
					text-align: center;
					background-color: rgba(0,0,0,.1);
					font-size: 20px;
					color: #ffffff;
					top: 50%;
					margin-top: -25px;
					cursor: pointer;
					font-family: "宋体";
					&.prev {
						left: 0;
					}
					&.next {
						right: 0;
					}
					&:hover {
						background-color: rgba(0,0,0,.5);
					}
				}
			}
		}
	}
	.fade-enter-active, .fade-leave-active {
	  transition: opacity .3s;
	}
	.fade-enter, .fade-leave-to {
	  opacity: 0;
	}
</style>
<style>
	.el-loading-spinner {
		position: fixed;
    top: 300px;
    margin-top: 0;
  }
</style>



